body {
  margin: 0;
  padding: 0;
  font-family: Roboto,-apple-system,Helvetica Neue,Helvetica,Segoe UI,Arial,sans-serif;
  -webkit-font-smoothing: antialiased;
}

.exchange-from:before {
  content: '-';
}

.exchange-to:before {
  content: '+';
}

.rate-eq {
  padding: 0 4px;
}

.GBP:before {
  content: '£';
}

.USD:before {
  content: '$';
}

.EUR:before {
  content: '€';
}



.Px-12 {
  padding-left: 12px;
  padding-right: 12px;
}

.exchange-divider {
  text-align: center;
  position: relative;
  height: 54px;
}

.exchange-separator {
  top: 50%;
  position: relative;
}

.exchange-select {
  width: 100%;
  padding-right: 8px !important;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.exchange-swap {
  position: absolute;
  left: 0;
  vertical-align: middle;
}

.exchange-rate {
  vertical-align: middle;
}

.exchange-digit:nth-last-child(1), .exchange-digit:nth-last-child(2) {
  font-size: 12px;
}

.exchange-amount input{
  text-align: right;
}

.exchange-hint {
  color: red !important;
  text-align: right !important;
}


